@import url("https://fonts.googleapis.com/css2?family=Bagel+Fat+One&family=Gugi&family=Moirai+One&family=Sunflower:wght@300&display=swap");
@font-face {
  font-family: 'TTHakgyoansimMulgyeolB';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2402_keris@1.0/TTHakgyoansimMulgyeolB.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'GangwonEduHyeonokT_OTFMediumA';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEduHyeonokT_OTFMediumA.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'IBMPlexSansKR-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/IBMPlexSansKR-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

html, body {
  font-size: 62.35%;/* 10px */
}

body {
  line-height: 1;
  color: #333;
}

#wrap {
  height : auto;
  min-height: 100%;
  padding-bottom: 80px;
}

.header .inner {
  height: 80px;
  max-width: 767px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  padding: 0 10px;

  h1 {
    font-family: "Gugi", sans-serif;
    font-size: 40px;
    color: #0275c2;
    span {
  position: relative;
  top: 20px;
  display: inline-block;
  animation: bounce .5s ease infinite alternate;
    }
  }
  .menu {
    img {
      width: 40px;
      height: 40px;
    }
  }
h1 span:nth-child(2) { animation-delay: .1s; }
h1 span:nth-child(3) { animation-delay: .2s; }
h1 span:nth-child(4) { animation-delay: .3s; }
h1 span:nth-child(5) { animation-delay: .4s; }
h1 span:nth-child(6) { animation-delay: .5s; }
h1 span:nth-child(7) { animation-delay: .6s; }
h1 span:nth-child(8) { animation-delay: .7s; }
}
@keyframes bounce {
  100% {
    top: -5px;
    text-shadow: 0 1px 0 /* #CCC */,
                 0 2px 0 /* #CCC */,
                 0 3px 0/*  #CCC */,
                 0 4px 0 /* #CCC */,
                 0 5px 0/*  #CCC */,
                 0 6px 0/*  #CCC */,
                 0 7px 0 /* #CCC */,
                 0 8px 0 /* #CCC */,
                 0 9px 0 /* #CC */C,
                 0 50px 25px rgba(0, 0, 0, .2);
  }
}

.main_visual {
  .inner {
    max-width: 767px;
    margin: 0 auto;
    position: relative;
    

    figure {
      max-height: 400px;
      height: 52.15vw;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .text_wrap{
      display: block;
      position: absolute;
      top:50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 747px;
      max-height: 350px;
      height: 46.85vw;
      border: 5px solid #ffffff96;
      .text{
        font-family: 'TTHakgyoansimMulgyeolB';
        width: 100%;
        text-align: center;
        position: absolute;
        top:50%;
        left: 0;
        transform: translateY(-50%);
      h3 {
        text-align: center;
        color: #fff;
        font-size: 2.5rem;
        margin-bottom: 2rem;
      }
      p {
        text-align: center;
        color: #fff;
        font-size: 2rem;
      }
    }
    }
    .btn {
      width: 100%;
      text-align: center;
      position: absolute;
      bottom: 3rem;
      left: 0;

    }
    button {
      background-color: rgba(255, 255, 255, 0.418);
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: 1px solid #fff;
      font-weight: 700;
    }
  }
}

.main_visual .inner .search{
  position:absolute;
  top:30%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 99;
}
.main_visual .inner .search input[type="text"] {/* input 선택자  */
  width:400px;
  height: 30px;
  border-radius: 30px;
  border: 1px solid #333;
  padding: 0 10px;
  background-color: #ffffff7c;
}
.main_visual .inner .search input::placeholder {
  color: #99999971;
  font-size: 13px;
  letter-spacing: -1px;
  position:relative;
}
.main_visual .inner .search img {
  vertical-align: middle; /* inline 요소 또는 display:inline,
                              display:inline-block한테만 사용가능
                              수직정렬 : top, middle, bottom */
  position: absolute;
  left:93%;
  top:50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
}

.cont02 {
  padding: 70px 0;
  .inner {
    max-width: 767px;
    margin: 0 auto;

    .list {
      display: flex;
      justify-content: space-between;

      .item {
        width: calc(100% / 3 - 20px);
        max-height: 100px;
        height: 13.03vw;
        border-radius: 10px;
        position: relative;
        h4 {
          width: 100%;
          font-size: 2rem;
          text-align: center;
          position: absolute;
          top:50%;
          left: 0;
          transform: translateY(-5);
          color: #333;
          font-family: 'IBMPlexSansKR-Regular';

        }
      }
    }
  }
}

.cont02 .inner .list .item::before {
  content: '';
  opacity: 0.5;
  position: absolute;
  top:0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
}

.cont02 .inner .list .item:first-child {
  background: url(../public/img/23601.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.cont02 .inner .list .item:nth-child(2) {
  background: url(../public/img/2106.i105.015.S.m005.c13.isometric\ hotel\ flowchart.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.cont02 .inner .list .item:nth-child(3) {
  background: url(../public/img/16592.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.tour_list {
  max-width: 767px;
  width: calc(100% - 30px);
  margin: 0 auto;
}

.tour_list .item {
  font-family: 'GangwonEduHyeonokT_OTFMediumA';
  position: relative;
}
.tour_list .item:first-child {
  background-color: rgb(84, 197, 131);
  border-radius: 200px 20px 200px 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 500px;
}

.tour_list .item h4 {
  margin-top: 1rem;
  text-align: left;
  font-size: 2.5rem;
}

.tour_list .item p {
  display: block;
  margin-top: 1rem;
  font-size: 2rem;
  padding: 0 15px;
  position: relative;
  background: url(../public/img/icons8-오른쪽-화살표-64.png)center left/ 15px 15px no-repeat;
}

.tour_list .item .gu {
  position: absolute;
  font-family: 'Pretendard-Regular';
  display: block;
  font-size: 1.5rem;
  border-radius: 10px;
  width: 70px;
  height: 30px;
  text-align: center;
