.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}

.footer .inner {
  max-width: 767px;
  margin: 0 auto;
}

.footer .inner  .list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  height: 80px;
  
}

.footer .inner .list .footer_menu {
  width: calc(100% / 4 - 20px);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: relative;
}

.footer .inner .list .footer_menu a {
  display: block;
  width: 100%;
}

.footer .inner .list .footer_menu img {
  width: 30px;
  height: 30px;
}

.footer .inner .list .footer_menu .txt {
  font-size: 1.4rem;
  padding-top: 1rem;
}

.footer .inner .list .footer_menu:first-child {
  background-color: #e2dfff;
  height: 100%;
}

.footer .inner .list .footer_menu:nth-child(3) {
  background-color: #e2dfff;
  height: 100%;
}

.footer .inner .list .footer_menu::after {
  content: '';
  border-left: 1px dotted #333;
  position: absolute;
  top:0;
  left: 0;
  display: block;
}